@import "../../styles/mixins";
@import "../../styles/variables";

.navigation {
    width: 100%;
    display: flex;
    justify-content: space-between;

    position: relative;
    &__links {
        display: flex;
        align-items: center;
        visibility: hidden;
    }
    &__link {
        color: $link;
        font-weight: $regular;
        margin-left: rem(25);
        font-size: rem($fz-link);
        text-transform: uppercase;
    }
    &__logo {
        img {
            width: auto;
            height: rem(92);
        }
    }
    a {
        transition: 0.3s;
        &:hover {
            color: $link-hover;
        }
    }
}

.banner {
    display: none;
    &__top {
        position: absolute;
        top: 0;
        left: 0;
        z-index: -1;
        img {
            width: 100%;
            height: auto;
        }
    }
    &__bottom {
        position: absolute;
        bottom: 0;
        left: 0;
        z-index: -1;
        img {
            width: 100%;
            height: auto;
        }
    }
}
