@import "../../styles/mixins";
@import "../../styles/variables";

.dashboard {
    flex: 1 1 auto;
    &__title {
        color: $title;
        font-style: italic;
        text-transform: uppercase;
    }
    &__list {
        display: grid;
        grid-template-columns: repeat(auto-fill, rem(150));
        justify-content: space-between;
        column-gap: rem(15);
        row-gap: rem(15);
        margin-bottom: rem(30);
    }
    &__item {
        font-weight: 300;

        a {
            display: grid;
            grid-template-rows: 1fr auto;
            transition: 0.4s;
            color: $black;
            text-align: center;
            img {
                width: 100%;
                height: rem(150);
                margin-bottom: rem(5);
            }
            &:hover {
                transform: translate(rem(-5), rem(-5));
                box-shadow: 6px 3px 9px 2px rgba(34, 60, 80, 0.6);
            }
        }
    }
    &__name {
        margin-top: 0;
        margin-bottom: rem(5);
        font-size: rem($fz-title);
        color: $title;
        font-style: italic;
        text-transform: uppercase;
        &--center {
            text-align: center;
            color: $black;
            font-size: rem($fz-text);
            font-weight: 700;
        }
    }
    &__text {
        margin-top: 0;
        font-size: rem($fz-subtext);
        color: $gray;
    }
    &__social {
        display: inline-block;
        padding: rem(10) rem(36);
        background: $button;
        border-radius: rem(30);
        transition: 0.3s;
        color: $white !important;
        font-weight: 700;
        &:hover {
            background: $button-hover;
        }
    }
    &__social-inner {
        display: flex;
        align-items: center;
    }
    &__describe {
        text-align: center;
    }

}

.footer {
    position: relative;
    height: rem(61);
    padding: rem(5) rem(15);
    display: flex;
    align-items: center;
    &__img {
        position: absolute;
        width: 50%;
        left: 0;
        top: rem(-8);
        opacity: 0.5;
    }
    &__img-second {
        position: absolute;
        width: 50%;
        top: rem(-8);
        right: 0;
        opacity: 0.5;
    }
    span {
        font-weight: 300;
    }
    svg {
        margin: 0 rem(15);
    }
}

@media (max-width: 675px) {
    .footer {
        &__img {
            display: none;
        }
        &__img-second {
            display: none;
        }
    }
}
@media (max-width: 510px) {
    .dashboard {
        &__list {
            justify-content: center;
        }
    }
}