@import "./styles/mixins";
@import "./styles/variables";
@import "./styles//fonts";

body {
    @include reset;
    font-family: "Roboto", sans-serif;
}

#root, html, body {
    height: 100%;
}

button {
    @include button-reset;
}

ul,
li {
    @include list-reset;
}

a {
    text-decoration: none;
    color: inherit;
}


.home {
    position: relative;
    height: 100%;
    &__wrapper {
        @include push--auto;
        max-width: rem(1110);
        padding: 0 rem(15);
        min-height: 100%;
        display: flex;
        flex-direction: column;
    }
}
